      // ÏÇ¨Ïö©ÏûêÏóêÍ≤å Ï¶âÏãú Îã§Ïö¥Î°úÎìú Ï†úÍ≥µ
      const url = window.URL.createObjectURL(blob);
      const a = document.createElement('a');
      a.href = url;
      a.download = downloadFilename;
      document.body.appendChild(a);
      a.click();
      document.body.removeChild(a);
      window.URL.revokeObjectURL(url);
      
      toast.success('ÏóëÏÖÄ ÌååÏùºÏù¥ Îã§Ïö¥Î°úÎìúÎêòÏóàÏäµÎãàÎã§.');
      
      // DBÏóê Îã§Ïö¥Î°úÎìú ÏôÑÎ£å ÌîåÎûòÍ∑∏(is_po_download) ÏóÖÎç∞Ïù¥Ìä∏ - lead buyerÎßå Ìï¥Îãπ
