import { useState, useMemo, useEffect, useCallback, useRef } from 'react';
import { useDebounce } from '@/hooks/useDebounce';
import { Purchase } from './usePurchaseData';
import { createClient } from '@/lib/supabase/client';

// ÏÉÅÏàò Ï†ïÏùò - ÌäπÏ†ï ÏßÅÏõêÏùò Î∞úÏ£ºÏöîÏ≤≠ Ïà®ÍπÄ (Î≥∏Ïù∏Ïù¥ ÏïÑÎãå Í≤ΩÏö∞ÏóêÎßå)
const HIDDEN_EMPLOYEES = ['Ï†ïÌù¨ÏõÖ'];  // Ï†ïÌòÑÏõÖ Ï†úÍ±∞

// Î©îÎ™®Ïù¥Ï†úÏù¥ÏÖò Ï∫êÏãú
const filterCache = new Map();
const CACHE_SIZE_LIMIT = 100;

export const useFastPurchaseFilters = (purchases: Purchase[], currentUserRoles: string[], currentUserName: string, currentUserId?: string, currentUserEmail?: string) => {
  const supabase = createClient();
  const [activeTab, setActiveTab] = useState('pending');
  const [searchTerm, setSearchTerm] = useState('');
  const [vendorFilter, setVendorFilter] = useState('');
  // Ï¥àÍ∏∞Í∞í ÏÑ§Ï†ï - hanslwebappÍ≥º ÎèôÏùºÌïòÍ≤å Îπà Î¨∏ÏûêÏó¥Î°ú ÏãúÏûë
  const [selectedEmployee, setSelectedEmployee] = useState<string>('');
  const [purchaseNumberFilter, setPurchaseNumberFilter] = useState('');
  const [itemNameFilter, setItemNameFilter] = useState('');
  const [specificationFilter, setSpecificationFilter] = useState('');
  const [approvalStatusFilter, setApprovalStatusFilter] = useState('');
  const [remarkFilter, setRemarkFilter] = useState('');
  
  // Í∏∞Í∞Ñ ÌïÑÌÑ∞ Ï¥àÍ∏∞Í∞í ÏÑ§Ï†ï (Ïò¨Ìï¥ 1Ïõî 1Ïùº ~ Ïò§Îäò)
  const thisYear = new Date().getFullYear();
  const defaultStart = new Date(thisYear, 0, 1).toISOString().split('T')[0];
  const defaultEnd = new Date().toISOString().split('T')[0];
  const [dateFromFilter, setDateFromFilter] = useState(defaultStart);
  const [dateToFilter, setDateToFilter] = useState(defaultEnd);
  
  const debouncedSearchTerm = useDebounce(searchTerm, 300);
  
  // Í∂åÌïúÎ≥Ñ Î°úÏßÅ Í≥ÑÏÇ∞
  const isAdmin = currentUserRoles?.includes('app_admin');
  const isFinalApprover = currentUserRoles?.includes('final_approver');
  const isMiddleManager = currentUserRoles?.includes('middle_manager');
  const isPurchaseManager = currentUserRoles?.includes('purchase_manager');
  const hasApprovalRole = isAdmin || isFinalApprover || isMiddleManager;
  
  // lead buyer Í∂åÌïú Ï≤¥ÌÅ¨ Ï∂îÍ∞Ä
  const isLeadBuyer = currentUserRoles?.includes('raw_material_manager') || 
                      currentUserRoles?.includes('consumable_manager') || 
                      currentUserRoles?.includes('purchase_manager');

  // hanslwebappÍ≥º ÎèôÏùºÌïú Î°úÏßÅ - roleCase Í≥ÑÏÇ∞ (lead buyer Ï∂îÍ∞Ä)
  const roleCase = useMemo(() => {
    if (!currentUserRoles || currentUserRoles.length === 0) return 1; // null
    if (isPurchaseManager) return 2; // purchase_manager
    if (currentUserRoles.some(r => ['middle_manager', 'final_approver', 'app_admin', 'ceo'].includes(r))) return 3;
    return 1;
  }, [currentUserRoles, isPurchaseManager]);
  
  // ÌÉ≠Î≥Ñ Í∏∞Î≥∏ ÏßÅÏõê ÌïÑÌÑ∞ Í≥ÑÏÇ∞ (Íµ¨Îß§ÌòÑÌô©ÏùÄ lead buyerÏôÄ app_adminÎßå Ï†ÑÏ≤¥ Î≥¥Í∏∞)
  const computeDefaultEmployee = useCallback(
    (tabKey: string): string => {
      if (!currentUserName) return 'all';
      
      // Íµ¨Îß§ÌòÑÌô© ÌÉ≠ÏùÄ lead buyerÏôÄ app_adminÎßå Ï†ÑÏ≤¥ Î≥¥Í∏∞
      if (tabKey === 'purchase') {
        if (isLeadBuyer || isAdmin) {
          return 'all';
        }
        return currentUserName;
      }
      
      switch (roleCase) {
        case 1: // role null
          if (tabKey === 'done') return 'all';
          return currentUserName;
        case 2: // purchase_manager
          if (tabKey === 'done') return 'all';
          return currentUserName; // pending & receipt
        case 3: // Í¥ÄÎ¶¨Ïûê Í∂åÌïú
          return 'all';
        default:
          return currentUserName;
      }
    },
    [currentUserName, roleCase, isLeadBuyer, isAdmin]
  );
  
  // ÌÉ≠ Î≥ÄÍ≤Ω ÎòêÎäî ÏÇ¨Ïö©Ïûê/Ïó≠Ìï† Î°úÎî© Ïãú Í∏∞Î≥∏Í∞í ÏÑ§Ï†ï (hanslwebappÍ≥º ÎèôÏùº)
  useEffect(() => {
    if (!currentUserName) return;
    const defaultEmployee = computeDefaultEmployee(activeTab);
    setSelectedEmployee(defaultEmployee);
  }, [activeTab, currentUserName, roleCase, computeDefaultEmployee]);
  
  // 1Îã®Í≥Ñ: Í∂åÌïúÎ≥Ñ ÌïÑÌÑ∞ÎßÅ (Ï∫êÏã± Ï†ÅÏö©)
  const visiblePurchases = useMemo(() => {
    console.log('üîç [Filter] 1Îã®Í≥Ñ Í∂åÌïúÎ≥Ñ ÌïÑÌÑ∞ÎßÅ ÏãúÏûë:', {
      purchasesCount: purchases.length,
      currentUserRoles,
      currentUserName
    });
    
    try {
      const cacheKey = `visible_${purchases.length}_${currentUserRoles.join(',')}`;
      if (filterCache.has(cacheKey)) {
        console.log('üì¶ [Filter] Ï∫êÏãúÏóêÏÑú Í∞ÄÏ†∏Ïò¥');
        return filterCache.get(cacheKey);
      }
      
      let result;
      if (currentUserRoles.includes('purchase_manager') || currentUserRoles.includes('app_admin')) {
        result = purchases;
      } else {
        result = purchases.filter(p => !HIDDEN_EMPLOYEES.includes(p.requester_name));
      }
      
      console.log('‚úÖ [Filter] Í∂åÌïúÎ≥Ñ ÌïÑÌÑ∞ÎßÅ ÏôÑÎ£å:', {
        originalCount: purchases.length,
        filteredCount: result.length
      });
      
      // Ï∫êÏãú ÌÅ¨Í∏∞ Ï†úÌïú
      if (filterCache.size >= CACHE_SIZE_LIMIT) {
        const firstKey = filterCache.keys().next().value;
        filterCache.delete(firstKey);
      }
      filterCache.set(cacheKey, result);
      return result;
    } catch (error) {
      console.error('ÌïÑÌÑ∞ÎßÅ Ï§ë Ïò§Î•ò:', error);
      // Ï∫êÏãú Ï¥àÍ∏∞Ìôî ÌõÑ ÏßÅÏ†ë Í≥ÑÏÇ∞
      filterCache.clear();
      if (currentUserRoles.includes('purchase_manager') || currentUserRoles.includes('app_admin')) {
        return purchases;
      } else {
        return purchases.filter(p => !HIDDEN_EMPLOYEES.includes(p.requester_name));
      }
    }
  }, [purchases, currentUserRoles]);

  // 2Îã®Í≥Ñ: ÎÇ†Ïßú ÌïÑÌÑ∞ÎßÅ (Ï∫êÏã± Ï†ÅÏö©)
  const dateFilteredPurchases = useMemo(() => {
    const cacheKey = `date_${visiblePurchases.length}_${dateFromFilter}_${dateToFilter}`;
    if (filterCache.has(cacheKey)) {
      return filterCache.get(cacheKey);
    }
    
    const result = visiblePurchases.filter((purchase: Purchase) => {
      const requestDate = purchase.request_date ? purchase.request_date.split('T')[0] : '';
      const matchesDateFrom = !dateFromFilter || requestDate >= dateFromFilter;
      const matchesDateTo = !dateToFilter || requestDate <= dateToFilter;
      return matchesDateFrom && matchesDateTo;
    });
    
    if (filterCache.size >= CACHE_SIZE_LIMIT) {
      const firstKey = filterCache.keys().next().value;
      filterCache.delete(firstKey);
    }
    filterCache.set(cacheKey, result);
    return result;
  }, [visiblePurchases, dateFromFilter, dateToFilter]);

  // 3Îã®Í≥Ñ: ÌÉ≠Î≥Ñ ÌïÑÌÑ∞ÎßÅ (ÏµúÏ†ÅÌôî Ï†ÅÏö©)
  const tabFilteredPurchases = useMemo(() => {
    const cacheKey = `tab_${dateFilteredPurchases.length}_${activeTab}`;
    if (filterCache.has(cacheKey)) {
      return filterCache.get(cacheKey);
    }
    
      // ÎîîÎ≤ÑÍπÖ: pending ÌÉ≠Ïùº Îïå Ï†ÑÏ≤¥ Îç∞Ïù¥ÌÑ∞ ÏÉÅÌÉú Î°úÍ∑∏
      if (activeTab === 'pending') {
        console.log('üîç [PENDING TAB DEBUG] Ï†ÑÏ≤¥ Îç∞Ïù¥ÌÑ∞ ÏÉÅÌÉú:', {
          totalCount: dateFilteredPurchases.length,
          sampleData: dateFilteredPurchases.slice(0, 5).map(p => ({
            id: p.id,
            po_number: p.purchase_order_number,
            middle_status: p.middle_manager_status,
            final_status: p.final_manager_status,
            requester: p.requester_name,
            middle_type: typeof p.middle_manager_status,
            final_type: typeof p.final_manager_status
          }))
        });
        
        // Î™®Îì† ÏÉÅÌÉúÍ∞í ÌôïÏù∏
        const allStatuses = dateFilteredPurchases.map(p => ({
          middle: p.middle_manager_status,
          final: p.final_manager_status
        }));
        const uniqueMiddleStatuses = [...new Set(allStatuses.map(s => s.middle))];
        const uniqueFinalStatuses = [...new Set(allStatuses.map(s => s.final))];
        
        console.log('üîç [PENDING TAB DEBUG] Í≥†Ïú† ÏÉÅÌÉúÍ∞íÎì§:', {
          uniqueMiddleStatuses,
          uniqueFinalStatuses
        });
      }
    
    const result = dateFilteredPurchases.filter((purchase: Purchase) => {
      let matches = false;
      
      switch (activeTab) {
        case 'pending':
          // Ï§ëÍ∞ÑÏäπÏù∏ÏûêÎÇò ÏµúÏ¢ÖÏäπÏù∏Ïûê Ï§ë ÌïòÎÇòÎùºÎèÑ pendingÏù¥Î©¥ ÏäπÏù∏ÎåÄÍ∏∞
          const middlePending = ['pending', 'ÎåÄÍ∏∞', '', null, undefined].includes(purchase.middle_manager_status as any);
          const finalPending = ['pending', 'ÎåÄÍ∏∞', '', null, undefined].includes(purchase.final_manager_status as any);
          
          // Î∞òÎ†§Îêú Í≤ΩÏö∞Îäî Ï†úÏô∏
          const middleRejected = purchase.middle_manager_status === 'rejected';
          const finalRejected = purchase.final_manager_status === 'rejected';
          
          if (middleRejected || finalRejected) return false;
          
          // Ï§ëÍ∞ÑÏäπÏù∏ ÎåÄÍ∏∞ ÎòêÎäî ÏµúÏ¢ÖÏäπÏù∏ ÎåÄÍ∏∞
          matches = middlePending || finalPending;
          
          // ÎîîÎ≤ÑÍπÖ Î°úÍ∑∏ Ï∂îÍ∞Ä
          if (matches) {
            console.log('üîç [PENDING DEBUG] ÏäπÏù∏ÎåÄÍ∏∞ Ìï≠Î™© Î∞úÍ≤¨:', {
              id: purchase.id,
              po_number: purchase.purchase_order_number,
              middle_status: purchase.middle_manager_status,
              final_status: purchase.final_manager_status,
              middlePending,
              finalPending,
              matches
            });
          }
          
          return matches;
          
        case 'purchase': {
          // DB ÌôïÏù∏ Í≤∞Í≥º: 'Íµ¨Îß§ ÏöîÏ≤≠' (ÎùÑÏñ¥Ïì∞Í∏∞ ÏûàÏùå) ÎòêÎäî 'Î∞úÏ£º'
          const isRequest = purchase.payment_category === 'Íµ¨Îß§ ÏöîÏ≤≠';
          const notPaid = !purchase.is_payment_completed;
          const isSeonJin = (purchase.progress_type || '').includes('ÏÑ†ÏßÑÌñâ');
          const isIlban = (purchase.progress_type || '').includes('ÏùºÎ∞ò');
          const finalApproved = purchase.final_manager_status === 'approved';
          
          if (!isRequest || !notPaid) {
            matches = false;
          } else {
            matches = (isSeonJin) || (isIlban && finalApproved);
          }
          
          return matches;
        }
        
        case 'receipt': {
          // ÏûÖÍ≥†ÌòÑÌô©: ÎØ∏ÏûÖÍ≥† & (ÏÑ†ÏßÑÌñâ or ÏµúÏ¢ÖÏäπÏù∏)
          const notReceived = !purchase.is_received;
          const isSeonJin = (purchase.progress_type || '').includes('ÏÑ†ÏßÑÌñâ');
          const finalApproved = purchase.final_manager_status === 'approved';
          matches = notReceived && (isSeonJin || finalApproved);
          return matches;
        }
        
        case 'done':
          matches = true;
          return matches;
          
        default:
          return true;
      }
    });
    
    if (filterCache.size >= CACHE_SIZE_LIMIT) {
      const firstKey = filterCache.keys().next().value;
      filterCache.delete(firstKey);
    }
    filterCache.set(cacheKey, result);
    return result;
  }, [dateFilteredPurchases, activeTab]);

  // 4Îã®Í≥Ñ: ÏßÅÏõê ÌïÑÌÑ∞ÎßÅ (ÏµúÏ†ÅÌôî Ï†ÅÏö©)
  const employeeFilteredPurchases = useMemo(() => {
    const cacheKey = `employee_${tabFilteredPurchases.length}_${selectedEmployee}`;
    if (filterCache.has(cacheKey)) {
      return filterCache.get(cacheKey);
    }
    
    let result;
    if (selectedEmployee && selectedEmployee !== 'all' && selectedEmployee !== 'Ï†ÑÏ≤¥') {
      result = tabFilteredPurchases.filter((purchase: Purchase) => purchase.requester_name === selectedEmployee);
    } else {
      result = tabFilteredPurchases;
    }
    
    if (filterCache.size >= CACHE_SIZE_LIMIT) {
      const firstKey = filterCache.keys().next().value;
      filterCache.delete(firstKey);
    }
    filterCache.set(cacheKey, result);
    return result;
  }, [tabFilteredPurchases, selectedEmployee]);

  // 5Îã®Í≥Ñ: ÏóÖÏ≤¥ ÌïÑÌÑ∞ÎßÅ (ÏóÖÏ≤¥ ÏÑ†ÌÉùÏãúÎßå Ïã§Ìñâ)
  const vendorFilteredPurchases = useMemo(() => {
    if (!vendorFilter) {
      return employeeFilteredPurchases;
    }
    return employeeFilteredPurchases.filter((purchase: Purchase) => purchase.vendor_name === vendorFilter);
  }, [employeeFilteredPurchases, vendorFilter]);

  // 6Îã®Í≥Ñ: Ï∂îÍ∞Ä ÌïÑÌÑ∞ Ï†ÅÏö©
  const additionalFilteredPurchases = useMemo(() => {
    let filtered = vendorFilteredPurchases;
    
    // Î∞úÏ£ºÏöîÏ≤≠Î≤àÌò∏ ÌïÑÌÑ∞
    if (purchaseNumberFilter) {
      const term = purchaseNumberFilter.trim().toLowerCase();
      filtered = filtered.filter((p: Purchase) => p.purchase_order_number?.toLowerCase().includes(term));
    }
    
    // ÌíàÎ™Ö ÌïÑÌÑ∞
    if (itemNameFilter) {
      const term = itemNameFilter.trim().toLowerCase();
      filtered = filtered.filter((p: Purchase) => {
        if (p.items && p.items.length > 0) {
          return p.items.some((item: any) => item.item_name?.toLowerCase().includes(term));
        }
        return false;
      });
    }
    
    // Í∑úÍ≤© ÌïÑÌÑ∞
    if (specificationFilter) {
      const term = specificationFilter.trim().toLowerCase();
      filtered = filtered.filter((p: Purchase) => {
        if (p.items && p.items.length > 0) {
          return p.items.some((item: any) => item.specification?.toLowerCase().includes(term));
        }
        return false;
      });
    }
    
    // ÏäπÏù∏ÏÉÅÌÉú ÌïÑÌÑ∞
    if (approvalStatusFilter && approvalStatusFilter !== 'all') {
      filtered = filtered.filter((p: Purchase) => {
        switch (approvalStatusFilter) {
          case 'pending':
            return !p.final_manager_status || p.final_manager_status === 'pending' || p.final_manager_status === 'ÎåÄÍ∏∞';
          case 'approved':
            return p.final_manager_status === 'approved';
          case 'rejected':
            return p.final_manager_status === 'rejected' || p.middle_manager_status === 'rejected';
          default:
            return true;
        }
      });
    }
    
    // ÎπÑÍ≥† ÌïÑÌÑ∞
    if (remarkFilter) {
      const term = remarkFilter.trim().toLowerCase();
      filtered = filtered.filter((p: Purchase) => {
        if (p.items && p.items.length > 0) {
          return p.items.some((item: any) => item.remark?.toLowerCase().includes(term));
        }
        return false;
      });
    }
    
    return filtered;
  }, [vendorFilteredPurchases, purchaseNumberFilter, itemNameFilter, specificationFilter, approvalStatusFilter, remarkFilter]);

  // 7Îã®Í≥Ñ: Í≤ÄÏÉâ ÌïÑÌÑ∞ÎßÅ (Í≤ÄÏÉâÏñ¥ Î≥ÄÍ≤ΩÏãúÎßå Ïã§Ìñâ)
  const searchFilteredPurchases = useMemo(() => {
    if (!debouncedSearchTerm) {
      return additionalFilteredPurchases;
    }
    
    const term = debouncedSearchTerm.trim().toLowerCase();
    
    return additionalFilteredPurchases.filter((purchase: Purchase) => {
      // Îπ†Î•∏ Í≤ÄÏÉâ (Í∏∞Î≥∏ ÌïÑÎìúÎßå)
      if (purchase.purchase_order_number?.toLowerCase().includes(term) ||
          purchase.vendor_name?.toLowerCase().includes(term) ||
          purchase.requester_name?.toLowerCase().includes(term) ||
          purchase.project_vendor?.toLowerCase().includes(term)) {
        return true;
      }
      
      // ÌíàÎ™© Í≤ÄÏÉâ (ÌïÑÏöîÌï† ÎïåÎßå)
      if (purchase.items && purchase.items.length > 0) {
        return purchase.items.some((item: any) => 
          (item.item_name && item.item_name.toLowerCase().includes(term)) ||
          (item.specification && item.specification.toLowerCase().includes(term))
        );
      }
      
      return false;
    });
  }, [additionalFilteredPurchases, debouncedSearchTerm]);

  // 8Îã®Í≥Ñ: ÏµúÏ¢Ö Ï†ïÎ†¨ - ÏµúÏã†Ïàú (ÎÇ¥Î¶ºÏ∞®Ïàú)
  const filteredPurchases = useMemo(() => {
    const result = [...searchFilteredPurchases].sort((a, b) => {
      // request_dateÎ•º Í∏∞Ï§ÄÏúºÎ°ú ÎÇ¥Î¶ºÏ∞®Ïàú Ï†ïÎ†¨ (ÏµúÏã†Ïù¥ ÏúÑÎ°ú)
      const dateA = a.request_date ? new Date(a.request_date).getTime() : 0;
      const dateB = b.request_date ? new Date(b.request_date).getTime() : 0;
      return dateB - dateA;
    });
    
    console.log('‚úÖ [Filter] ÏµúÏ¢Ö ÌïÑÌÑ∞ÎßÅ ÏôÑÎ£å:', {
      activeTab,
      searchFilteredCount: searchFilteredPurchases.length,
      finalCount: result.length,
      firstFewResults: result.slice(0, 3).map(p => ({
        id: p.id,
        po: p.purchase_order_number,
        requester: p.requester_name,
        date: p.request_date
      }))
    });
    
    return result;
  }, [searchFilteredPurchases, activeTab]);

  // ÌÉ≠ Ïπ¥Ïö¥Ìä∏ (hanslwebappÍ≥º ÎèôÏùºÌïú Ï°∞Í±¥)
  const tabCounts = useMemo(() => {
    // ÌäπÏ†ï ÏßÅÏõê Î∞úÏ£ºÏöîÏ≤≠ Ïà®ÍπÄ Ï≤òÎ¶¨
    const countPurchases = visiblePurchases;
    
    // Í∏∞Í∞Ñ ÌïÑÌÑ∞ Ï†ÅÏö©
    const dateFilteredForCount = countPurchases.filter((purchase: Purchase) => {
      const requestDate = purchase.request_date ? purchase.request_date.split('T')[0] : '';
      const matchesDateFrom = !dateFromFilter || requestDate >= dateFromFilter;
      const matchesDateTo = !dateToFilter || requestDate <= dateToFilter;
      return matchesDateFrom && matchesDateTo;
    });
    
    // Í∞Å ÌÉ≠Ïùò Í≥†Ïú† Î∞úÏ£ºÏöîÏ≤≠Î≤àÌò∏ Ïπ¥Ïö¥Ìä∏ (Ï§ëÎ≥µ Ï†úÍ±∞)
    const getUniqueOrderCount = (filtered: Purchase[]) => {
      return new Set(filtered.map(p => p.purchase_order_number)).size;
    };
    
    // Í∞Å ÌÉ≠Î≥ÑÎ°ú Í∏∞Î≥∏ ÏßÅÏõê ÌïÑÌÑ∞ Í≥ÑÏÇ∞ (Ïπ¥Ïö¥Ìä∏Ïö©)
    const getFilteredDataForTab = (tabKey: string) => {
      // Íµ¨Îß§ÌòÑÌô© ÌÉ≠ÏùÄ ÌäπÎ≥Ñ Ï≤òÎ¶¨
      if (tabKey === 'purchase') {
        if (isLeadBuyer || isAdmin) {
          return dateFilteredForCount;
        } else {
          return dateFilteredForCount.filter((p: Purchase) => p.requester_name === currentUserName);
        }
      }
      
      const defaultEmployee = computeDefaultEmployee(tabKey);
      
      if (defaultEmployee === 'all' || defaultEmployee === 'Ï†ÑÏ≤¥') {
        return dateFilteredForCount;
      } else {
        return dateFilteredForCount.filter((p: Purchase) => p.requester_name === defaultEmployee);
      }
    };
    
    // Í∞Å ÌÉ≠Î≥Ñ Îç∞Ïù¥ÌÑ∞ ÌïÑÌÑ∞ÎßÅ
    const pendingData = getFilteredDataForTab('pending');
    const purchaseData = getFilteredDataForTab('purchase');
    const receiptData = getFilteredDataForTab('receipt');
    const doneData = getFilteredDataForTab('done');
    
    const pendingFiltered = pendingData.filter((p: Purchase) => {
      // Ï§ëÍ∞ÑÏäπÏù∏ÏûêÎÇò ÏµúÏ¢ÖÏäπÏù∏Ïûê Ï§ë ÌïòÎÇòÎùºÎèÑ pendingÏù¥Î©¥ ÏäπÏù∏ÎåÄÍ∏∞
      const middlePending = ['pending', 'ÎåÄÍ∏∞', '', null, undefined].includes(p.middle_manager_status as any);
      const finalPending = ['pending', 'ÎåÄÍ∏∞', '', null, undefined].includes(p.final_manager_status as any);
      
      // Î∞òÎ†§Îêú Í≤ΩÏö∞Îäî Ï†úÏô∏
      const middleRejected = p.middle_manager_status === 'rejected';
      const finalRejected = p.final_manager_status === 'rejected';
      
      if (middleRejected || finalRejected) return false;
      
      // Ï§ëÍ∞ÑÏäπÏù∏ ÎåÄÍ∏∞ ÎòêÎäî ÏµúÏ¢ÖÏäπÏù∏ ÎåÄÍ∏∞
      return middlePending || finalPending;
    });
    
    const purchaseFiltered = purchaseData.filter((p: Purchase) => {
      const isRequest = p.payment_category === 'Íµ¨Îß§ ÏöîÏ≤≠';
      const notPaid = !p.is_payment_completed;
      if (!isRequest || !notPaid) return false;
      const isSeonJin = (p.progress_type || '').includes('ÏÑ†ÏßÑÌñâ');
      const isIlban = (p.progress_type || '').includes('ÏùºÎ∞ò');
      const finalApproved = p.final_manager_status === 'approved';
      return (isSeonJin) || (isIlban && finalApproved);
    });
    
    const receiptFiltered = receiptData.filter((p: Purchase) => {
      const notReceived = !p.is_received;
      const isSeonJin = (p.progress_type || '').includes('ÏÑ†ÏßÑÌñâ');
      const finalApproved = p.final_manager_status === 'approved';
      return notReceived && (isSeonJin || finalApproved);
    });
    
    const counts = {
      pending: getUniqueOrderCount(pendingFiltered),
      purchase: getUniqueOrderCount(purchaseFiltered),
      receipt: getUniqueOrderCount(receiptFiltered),
      done: getUniqueOrderCount(doneData)  // Ï†ÑÏ≤¥ Ìï≠Î™©
    };
    
    return counts;
  }, [visiblePurchases, dateFromFilter, dateToFilter, roleCase, currentUserName, computeDefaultEmployee, isLeadBuyer, isAdmin]);

  // ÏÇ¨Ïö©ÏûêÎ≥Ñ Ï†ÄÏû•Îêú Í∏∞Í∞Ñ Î∂àÎü¨Ïò§Í∏∞ (Î°úÎî© ÏµúÏ†ÅÌôî)
  const loadedPreferencesRef = useRef(false);
  useEffect(() => {
    if (loadedPreferencesRef.current) return;
    loadedPreferencesRef.current = true;
    
    (async () => {
      const { data: { user } } = await supabase.auth.getUser();
      if (!user) return;
      
      const { data } = await supabase
        .from('user_preferences')
        .select('period_start, period_end')
        .eq('user_id', user.id)
        .single();
        
      if (data) {
        const ps = data.period_start ? new Date(data.period_start).toISOString().split('T')[0] : defaultStart;
        const pe = data.period_end ? new Date(data.period_end).toISOString().split('T')[0] : defaultEnd;
        setDateFromFilter(ps);
        setDateToFilter(pe);
      }
    })();
  }, []);
  
  // Í∏∞Í∞Ñ Î≥ÄÍ≤Ω Ïãú ÎîîÎ∞îÏö¥Ïä§ Ï†ÄÏû• (ÏÇ¨Ïö©ÏûêÎ≥Ñ)
  const saveTimeoutRef = useRef<ReturnType<typeof setTimeout> | undefined>(undefined);
  useEffect(() => {
    if (!loadedPreferencesRef.current) return;
    
    if (saveTimeoutRef.current) {
      clearTimeout(saveTimeoutRef.current);
    }
    
    saveTimeoutRef.current = setTimeout(async () => {
      const { data: { user } } = await supabase.auth.getUser();
      if (!user) return;
      if (!dateFromFilter || !dateToFilter) return;
      
      await supabase.from('user_preferences').upsert({
        user_id: user.id,
        period_start: dateFromFilter,
        period_end: dateToFilter,
        updated_at: new Date().toISOString()
      });
    }, 1000);
    
    return () => {
      if (saveTimeoutRef.current) {
        clearTimeout(saveTimeoutRef.current);
      }
    };
  }, [dateFromFilter, dateToFilter]);

  return {
    // States
    activeTab,
    searchTerm,
    vendorFilter,
    dateFromFilter,
    dateToFilter,
    selectedEmployee,
    purchaseNumberFilter,
    itemNameFilter,
    specificationFilter,
    approvalStatusFilter,
    remarkFilter,
    
    // Setters
    setActiveTab,
    setSearchTerm,
    setVendorFilter,
    setDateFromFilter,
    setDateToFilter,
    setSelectedEmployee,
    setPurchaseNumberFilter,
    setItemNameFilter,
    setSpecificationFilter,
    setApprovalStatusFilter,
    setRemarkFilter,
    
    // Computed values
    filteredPurchases,
    tabCounts
  };
};